export default class Logger {
  /**
   * @param {any} initiator
   * @param {Object} options
   */
  constructor(initiator, options) {
    this.initiator = initiator;
    this._options = options;
  }

  /**
   * @param {string} t
   */
  stack(t) {
    console.info("[" + this.initiator + "] ", t);
    console.log(new Error().stack);
  }

  /**
   * @param {string} t
   */
  groupCollapsed(t) {
    if (
      (CABLES.UI &&
        CABLES.UI.logFilter.filterLog(
          { initiator: this.initiator, level: 0 },
          ...arguments,
        )) ||
      !CABLES.logSilent
    )
      console.log("[" + this.initiator + "]", ...arguments);

    console.groupCollapsed("[" + this.initiator + "] " + t);
  }

  /**
   * @param {any[][]} t
   */
  table(t) {
    console.table(t);
  }

  groupEnd() {
    console.groupEnd();
  }

  error() {
    if (
      (CABLES.UI &&
        CABLES.UI.logFilter.filterLog(
          { initiator: this.initiator, level: 2 },
          ...arguments,
        )) ||
      !CABLES.UI
    ) {
      console.error("[" + this.initiator + "]", ...arguments);
    }

    if (this._options && this._options.onError) {
      this._options.onError(this.initiator, ...arguments);
      // console.log("emitevent onerror...");
      // CABLES.patch.emitEvent("onError", this.initiator, ...arguments);
      // CABLES.logErrorConsole("[" + this.initiator + "]", ...arguments);
    }
  }

  errorGui() {
    if (CABLES.UI)
      CABLES.UI.logFilter.filterLog(
        { initiator: this.initiator, level: 2 },
        ...arguments,
      );
  }

  warn() {
    if (
      (CABLES.UI &&
        CABLES.UI.logFilter.filterLog(
          { initiator: this.initiator, level: 1 },
          ...arguments,
        )) ||
      !CABLES.logSilent
    )
      console.warn("[" + this.initiator + "]", ...arguments);
  }

  verbose() {
    if (
      (CABLES.UI &&
        CABLES.UI.logFilter.filterLog(
          { initiator: this.initiator, level: 0 },
          ...arguments,
        )) ||
      !CABLES.logSilent
    )
      console.log("[" + this.initiator + "]", ...arguments);
  }

  info() {
    if (
      (CABLES.UI &&
        CABLES.UI.logFilter.filterLog(
          { initiator: this.initiator, level: 0 },
          ...arguments,
        )) ||
      !CABLES.logSilent
    )
      console.info("[" + this.initiator + "]", ...arguments);
  }

  log() {
    if (
      (CABLES.UI &&
        CABLES.UI.logFilter.filterLog(
          { initiator: this.initiator, level: 0 },
          ...arguments,
        )) ||
      !CABLES.logSilent
    )
      console.log("[" + this.initiator + "]", ...arguments);
  }

  logGui() {
    if (CABLES.UI)
      CABLES.UI.logFilter.filterLog(
        { initiator: this.initiator, level: 0 },
        ...arguments,
      );
  }

  userInteraction(text) {
    // this.log({ "initiator": "userinteraction", "text": text });
  }
}
